"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { Save, User, Lock, Bell, Database } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Label } from "@/components/ui/label"
import { Separator } from "@/components/ui/separator"
import { getCurrentUser, authAPI } from "@/lib/api"
import { useToast } from "@/hooks/use-toast"

export default function AdminSettings() {
  const router = useRouter()
  const { toast } = useToast()
  const [user, setUser] = useState<any>(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Check authentication
    const currentUser = getCurrentUser()
    if (!currentUser || !currentUser.is_admin) {
      router.push("/login")
      return
    }
    setUser(currentUser)
    setIsLoading(false)
  }, [router])

  const handleSave = () => {
    toast({
      title: "Settings Saved",
      description: "Your settings have been saved successfully.",
    })
  }

  if (isLoading) {
    return (
      <div className="p-8 flex items-center justify-center min-h-screen">
        <p className="text-muted-foreground">Loading...</p>
      </div>
    )
  }

  return (
    <div className="p-8">
      <div className="mb-8">
        <h1 className="text-3xl font-bold">Settings</h1>
        <p className="text-muted-foreground" style={{ fontFamily: "var(--font-inter)" }}>
          Manage your account and application settings
        </p>
      </div>

      <div className="space-y-6 max-w-4xl">
        {/* Profile Settings */}
        <Card className="bg-white border-[#7BA4D0]/30">
          <CardHeader>
            <div className="flex items-center gap-2">
              <User className="h-5 w-5 text-[#2E5E99]" />
              <CardTitle>Profile Settings</CardTitle>
            </div>
            <CardDescription>Update your personal information</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-2 gap-4">
              <div>
                <Label htmlFor="first_name" style={{ fontFamily: "var(--font-inter)" }}>
                  First Name
                </Label>
                <Input
                  id="first_name"
                  defaultValue={user?.first_name || ""}
                  className="mt-2"
                  disabled
                />
              </div>
              <div>
                <Label htmlFor="last_name" style={{ fontFamily: "var(--font-inter)" }}>
                  Last Name
                </Label>
                <Input id="last_name" defaultValue={user?.last_name || ""} className="mt-2" disabled />
              </div>
            </div>
            <div>
              <Label htmlFor="email" style={{ fontFamily: "var(--font-inter)" }}>
                Email
              </Label>
              <Input id="email" defaultValue={user?.email || ""} className="mt-2" disabled />
            </div>
            <Button onClick={handleSave} className="gap-2">
              <Save className="h-4 w-4" />
              Save Changes
            </Button>
          </CardContent>
        </Card>

        {/* Security Settings */}
        <Card className="bg-white border-[#7BA4D0]/30">
          <CardHeader>
            <div className="flex items-center gap-2">
              <Lock className="h-5 w-5 text-[#2E5E99]" />
              <CardTitle>Security</CardTitle>
            </div>
            <CardDescription>Manage your password and security preferences</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div>
              <Label htmlFor="current_password" style={{ fontFamily: "var(--font-inter)" }}>
                Current Password
              </Label>
              <Input
                id="current_password"
                type="password"
                placeholder="Enter current password"
                className="mt-2"
              />
            </div>
            <div>
              <Label htmlFor="new_password" style={{ fontFamily: "var(--font-inter)" }}>
                New Password
              </Label>
              <Input
                id="new_password"
                type="password"
                placeholder="Enter new password"
                className="mt-2"
              />
            </div>
            <div>
              <Label htmlFor="confirm_password" style={{ fontFamily: "var(--font-inter)" }}>
                Confirm New Password
              </Label>
              <Input
                id="confirm_password"
                type="password"
                placeholder="Confirm new password"
                className="mt-2"
              />
            </div>
            <Button onClick={handleSave} className="gap-2">
              <Save className="h-4 w-4" />
              Update Password
            </Button>
          </CardContent>
        </Card>

        {/* Notification Settings */}
        <Card className="bg-white border-[#7BA4D0]/30">
          <CardHeader>
            <div className="flex items-center gap-2">
              <Bell className="h-5 w-5 text-[#2E5E99]" />
              <CardTitle>Notifications</CardTitle>
            </div>
            <CardDescription>Configure your notification preferences</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="flex items-center justify-between">
              <div>
                <Label style={{ fontFamily: "var(--font-inter)" }}>Email Notifications</Label>
                <p className="text-sm text-muted-foreground">
                  Receive email updates about orders and products
                </p>
              </div>
              <input type="checkbox" defaultChecked className="rounded" />
            </div>
            <Separator />
            <div className="flex items-center justify-between">
              <div>
                <Label style={{ fontFamily: "var(--font-inter)" }}>Order Alerts</Label>
                <p className="text-sm text-muted-foreground">Get notified when new orders arrive</p>
              </div>
              <input type="checkbox" defaultChecked className="rounded" />
            </div>
            <Separator />
            <div className="flex items-center justify-between">
              <div>
                <Label style={{ fontFamily: "var(--font-inter)" }}>Low Stock Alerts</Label>
                <p className="text-sm text-muted-foreground">
                  Receive alerts when products are running low
                </p>
              </div>
              <input type="checkbox" defaultChecked className="rounded" />
            </div>
            <Button onClick={handleSave} className="gap-2 mt-4">
              <Save className="h-4 w-4" />
              Save Preferences
            </Button>
          </CardContent>
        </Card>

        {/* System Information */}
        <Card className="bg-white border-[#7BA4D0]/30">
          <CardHeader>
            <div className="flex items-center gap-2">
              <Database className="h-5 w-5 text-[#2E5E99]" />
              <CardTitle>System Information</CardTitle>
            </div>
            <CardDescription>View system status and information</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-2 gap-4">
              <div>
                <Label style={{ fontFamily: "var(--font-inter)" }}>API Status</Label>
                <p className="text-sm text-green-600 font-medium mt-1">Online</p>
              </div>
              <div>
                <Label style={{ fontFamily: "var(--font-inter)" }}>Database Status</Label>
                <p className="text-sm text-green-600 font-medium mt-1">Connected</p>
              </div>
            </div>
            <Separator />
            <div>
              <Label style={{ fontFamily: "var(--font-inter)" }}>API Endpoint</Label>
              <p className="text-sm text-muted-foreground mt-1">
                {process.env.NEXT_PUBLIC_API_URL || "http://localhost:8000"}
              </p>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}

